import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {resetRace} from '../actions/resetRace';
import {placeBets} from '../actions/placeBets';
import {startRace} from '../actions/startRace';
import {endRace} from '../actions/endRace';
import {gameResult} from '../actions/gameResult';
import {updateFunds} from '../actions/updateFunds';

class RaceResult extends Component {

	resetRace() {
		this.props.resetRace();
		this.props.placeBets(this.props.raceDefaults.bets);
		this.props.startRace(this.props.raceDefaults.speeds);
		this.props.endRace(this.props.raceDefaults.positions);
		this.props.updateFunds(this.props.raceDefaults.totalFunds);
		this.props.gameResult(this.props.raceDefaults.finalResult);
	}

	render() {
		return (
				<div>
					<div className="row game-result">
						<div className="col-xs-4 game-reset"> 
							<img className="game-reset__image center-block" onClick={this.resetRace.bind(this)} title="Click to Reset Game" src="data:image/jpeg;base64," />
						</div>
						<div className="col-xs-4">
							<h4 class="text-center game-final-result"> {this.props.finalResult}  </h4>
						</div>
						<div className="col-xs-4 game-player">
							<img className="game-player__image center-block" title="Available Funds" src="data:image/png;base64," />
							<h3 className=" game-player__funds text-center"> ${this.props.totalFunds} </h3>
						</div>
					</div>
				</div>
			);
	}
}

function mapStateToProps (state) {
	return {
		cars: state.cars,
		raceDefaults: state.raceDefaults,
		totalFunds: state.totalFunds,
		finalResult: state.finalResult
	};
}

function matchDispatchToProps (dispatch) {
	return bindActionCreators({resetRace, placeBets, startRace, endRace, updateFunds, gameResult}, dispatch)
}

export default connect(mapStateToProps, matchDispatchToProps)(RaceResult);


